___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

header {
  background-color: #1F2937;
  color: white;
  text-align: center;
  padding: 1rem 0;
}

h1 {
  margin: 0;
}

#add {
  background-color: darkgrey;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

button {
  background-color: #1F2937;
  color: white;
  border: none;
  border-radius: 17px;
  padding: 0.6em 1.2em;
  font-size: 0.95rem;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #374151;
  cursor: pointer;
}

table {
  border-collapse: collapse;
  border: 2px solid white;
  font-size: 0.8rem;
  background-color: #1F2937;
  color: white;
  margin: 2rem auto;
  width: 90%;
  max-width: 1000px;
  text-align: center;
}

table caption {
  color: white;
  font-size: 1.8rem;
  font-weight: bold;
  padding: 10px;
}

td, th {
  border: 1px solid white;
  padding: 10px 20px;
}

form {
  display: flex;
  flex-direction: column;
  gap: 17px;
}

.form-element {
  display: flex;
  flex-direction: column;
}

.project-card {
  border: 1px solid #ccc;
  padding: 1em;
  margin: 1em auto;
  border-radius: 8px;
  background-color: #f9f9f9;
  color: black;
  max-width: 500px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.project-card h2 {
  margin-bottom: 0.5em;
}

.project-card ul {
  margin: 0.5rem 0;
  padding-left: 1.2rem;
}

#content {
  background-color: #111827;
  padding: 2rem;
  min-height: 100vh;
  box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.05);
}

dialog {
  border: none;
  border-radius: 12px;
  padding: 2em;
  background: white;
  color: black;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
  width: 90%;
  max-width: 400px;
}

input[type="checkbox"] {
  transform: scale(1.2);
}

.delete-btn {
  background-color: crimson;
  border: none;
  color: white;
  padding: 0.3em 0.8em;
  border-radius: 5px;
}

.delete-btn:hover {
  background-color: darkred;
}

/* Mobile responsiveness */
@media (max-width: 600px) {
  table, th, td {
    font-size: 0.7rem;
    padding: 6px;
  }

  #add {
    flex-direction: column;
    gap: 10px;
  }

  .project-card {
    padding: 0.6em;
    margin: 0.6em;
  }

  form {
    padding: 0.5rem;
  }

  .form-element input, .form-element select {
    font-size: 1rem;
    padding: 0.3em;
  }

  button {
    font-size: 0.9rem;
  }
}
